import DrawerInitiator from '../utils/drawer-initiator';

class App {
  constructor({
    menu, hero, main, drawer,
  }) {
    this._menu = menu;
    this._drawer = drawer;
    this._main = main;
    this._hero = hero;

    this._initialAppshell();
  }

  _initialAppshell() {
    DrawerInitiator.init({
      menu: this._menu,
      drawer: this._drawer,
      main: this._main,
      hero: this._hero,
    });
  }
}

export default App;
