@media screen and (max-width: 768px) {
    .nav {
        display: none;
    }

    .navmob {
        display: flex;
        position: fixed;
        top: 0;
        left: -100%;
        height: 100%;
        width: 100%;
        max-width: 100%;
        flex-direction: column;
        background-color: #135ea8;
        transition: all 0.3s ease;
    }

    .navmob.open {
        left: 0;
    }

    .navlistmob {
        display: flex;
        flex-direction: column;
        padding: 20px;
    }

    .navitemsmob {
        display: block;
        margin-bottom: 10px;
    }

    .card-list {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 10px;
        align-items: center;
    }

    .hero {
        width: 100%;
    }

    .heroinner {
        max-width: 100%;
    }

    .containerCard {
        padding: 10px;
    }

    .custom-element {
        position: relative;
        margin-top: 0;
    }

    .logomob {
        margin-left: 20px;
    }

    .menumob {
        display: flex;
    }
}

