    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Quicksand', sans-serif;
}

body, html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

body {
    font-size: 12px;
    color: #424242;
}

.menumob {
    background-color: #135ea8;
    color: white !important;
    width: 100%;
    display: flex;
    font-weight: 600;
    font-size: 20px;
    padding: 10px;
    justify-content: space-between;
}

.logomob {
    color: white;
    font-weight: 600;
    font-size: 20px;
}

.navmob {
    background-color: #135ea8;
    width: 100%;
    display: flex;
    font-weight: 600;
    font-size: 14px;
    text-transform: uppercase;
}

.navlistmob {
    width: 60%;
    padding: 0;
    margin: 0;
    text-align: left;
}

.navitemsmob {
    box-sizing: border-box;
    display: inline-block;
    text-align: right;
    line-height: 24px;
    text-transform: uppercase;
    color: white;
    font-size: 20px;
}

.iconmenu a {
    text-decoration: none;
    color: white;
}

.navmob a {
    display: inline-block;
    padding: 1.3em;
    text-decoration: none;
}

#drawer {
    width: 250px;
    height: 100%;
    position: absolute;
    transform: translate(-250px, 0);
    transition: transform 0.3s ease-in-out;
    background-color: #111;
}

#drawer.open {
    transform: translate(0, 0);
}

.nav {
	background-color: #135ea8;
	width: 100%;
	display: flex;
	font-weight: 600;
	font-size: 14px;
	text-transform: uppercase
}

.logo {
    width: 20%;
    padding: 0;
    margin: 0;
    text-align: left;
    box-sizing: border-box;
    display: inline-block;
    line-height: 24px;
    z-index: 200;
}

.navlist {
    width: 80%;
    padding: 0;
    margin: 0;
    text-align: right;
}

.navitems {
    box-sizing: border-box;
    display: inline-block;
    text-align: right;
    line-height: 24px;
    text-transform: uppercase;
}

.nav a {
    display: inline-block;
    padding: 1.3em;
    text-decoration: none;
    color: white !important;
}

.nav a:hover {
    text-decoration: none;
}

.iconmenu {
    width: 50px;
  height: 50px;
  min-width: 50px;
  min-height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.iconmenu a {
	display: inline-block;
	font-size: 24px;
    width: 100%;
    height: 100%;
  }


.hero {
    background-image: linear-gradient(rgba(0, 0, 5, .5), rgba(0, 0, 5, .5)), url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    display: flex;
    align-items: center;
    min-height: 380px;
    width: 100%;
    text-align: center;
    background-position: center;
    background-color: black;
    padding: 0 20px;
}

.heroinner {
	margin: 0 auto;
	max-width: 800px
}
.herotitle {
	color: #fff;
	font-weight: 600;
	font-size: 36px;
	text-shadow: 1px 1px #141414
}
.herosubtitle {
	color: #fff;
	margin-top: 16px;
	font-size: 18px;
	font-weight: 600;
	text-shadow: 1px 1px #141414
}
main {
    width: 100%;
    margin: 0 auto;
    box-shadow: none;
}

.content {
    padding: 32px;
}

.latest {
    width: 100%;
    margin-top: 30px;
    margin-bottom: 50px;
    text-align: center;
}

.latest h1 {
    font-weight: 700;
    font-size: 2em;
    margin-bottom: 50px;
}

h1 {
    text-align: center;
    font-style: italic;
}

.card-list {
    box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    display: grid;
    grid-template-columns: repeat(4, 1fr); /* Tampilkan 5 kolom */
    grid-gap: 20px;
    justify-content: center;
    align-items: center;
}

.containerCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.containerCard img{
	width: 360px;
}

.custom-element {
    text-align: center;
    padding: 20px;
    background-color: #f0f0f0;
    margin-top: 30px;
}

.skip-link {
    position: absolute;
    left: -10000px;
    top: auto;
    width: 1px;
    height: 1px;
    overflow: hidden;
}

.skip-link:focus {
    position: static;
    width: auto;
    height: auto;
